          <p class="project-desp"> A Full-Stack project for a forum app</p>
          <p class="project-desp"> Capstone Project for <b>General Assembly Cognizant Java Academy</b></p>
          <p class="project-desp"> Frontend developed by <b>HTML</b>, <b>JavaScript</b> </p>
          <p class="project-desp"> Backend developed by <b>Java</b>, <b>Spring Boot</b>, <b>PostgreSQL</b>, <b>RabbitMQ</b></p>
          <p class="project-desp"> Link : <a href="https://github.com/mingjingtang/postit2/tree/dev"> Source Code </a> </p>
        </li>
