    </div>
    <div class="project-block" id="project2">
      <ul>
        <li id="left">
          <img id="project-icon" src="../assets/project2.png" alt="" />
        </li>
        <li id="right">
          <p class="project-title"> <a href="https://www.youtube.com/watch?v=1bu7XMikHMw">Computer Vision and Machine Learning</a> </p>
          <p class="project-desp"> Position @ <a href="https://characterfacegen.com/">Vidalign, Inc</a></p>
          <p class="project-desp"> Major Contributor to 3D face mesh generation software projects</p>
          <p class="project-desp"> Responsbility: algorithm improvement, software development, software security </p>
          <p class="project-desp"> Tech Stack: <b>C++</b>, <b>Python</b>, <b>Deep Learning</b>, <b>GPU Computing</b>, <b>QT framework</b></p>
          <p class="project-desp"> Link : <a href="https://www.youtube.com/watch?v=1bu7XMikHMw"> Demo Video </a> </p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Projects',
  mounted(){
    // document.getElementById("skills").style.borderStyle = 'none';
  }
}
