<style scoped>

div.project-block{
  height: 300px;
  position: relative;
  top: 20px;
  bottom: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
  border: 1px solid;
  border-radius: 10px;
  padding: 10px;
}

div.project-block:hover{
  box-shadow: 5px 10px;
  transform: translate(-5px, -5px);
}

div#name-title{
border-style: solid;
border-width: 5px;
border-color:#333;
border-radius: 10px;
margin-top: 20px;
}

div#name-title{
-webkit-animation: slide-down 1.5s ease-out;
-moz-animation: slide-down 1.5s ease-out;
}

div#project1{
  -webkit-animation: slide-right 1.5s ease-out;
  -moz-animation: slide-right 1.5s ease-out;
}

div#project2{
  -webkit-animation: slide-left 1.5s ease-out;
  -moz-animation: slide-left 1.5s ease-out;
}

li {
  display: inline;
}

li#left{
  width:35%;
  float:left;
}

li#right{
  width:55%;
  float:right;
  align-content: center;
  margin-left: -200px;
}

img#project-icon {
  height: 280px;
  width: 280px;
}

a {
  text-decoration: none;
  color: firebrick;
  font-weight: bold;
}

a:hover{
  text-decoration: underline;
  font-style: oblique;
}

p.project-title{
  font-size: 24px;
  text-align: left;
  animation: shaky 2s linear infinite 2s;
}

p.project-title:hover{
  animation:none;
}

p.project-desp{
  text-align: left;
}

@-webkit-keyframes slide-down {
0% { opacity: 0; -webkit-transform: translateY(-100%); }
100% { opacity: 1; -webkit-transform: translateY(0); }
}
@-moz-keyframes slide-down {
0% { opacity: 0; -moz-transform: translateY(-100%); }
100% { opacity: 1; -moz-transform: translateY(0); }
}

@-webkit-keyframes slide-right {
0% { opacity: 0; -webkit-transform: translateX(-100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

@-moz-keyframes slide-right {
0% { opacity: 0; -webkit-transform: translateX(-100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

@-webkit-keyframes slide-left {
0% { opacity: 0; -webkit-transform: translateX(100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

@-moz-keyframes slide-left {
0% { opacity: 0; -webkit-transform: translateX(100%); }
100% { opacity: 1; -webkit-transform: translateX(0); }
}

@keyframes shaky {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}

</style>



