<template>
  <div id= "intro">
    <ul>
      <li>
        <div id="intro-left-column">
          <img id="round-image" src="../assets/photo.jpg" alt="" />
          <div id="intro-contact">
            <p> New York, NY</p>
            <p>
              <router-link to="/contact">
                <button id="contactme" type="button"> Contact Me </button>
              </router-link>
            </p>
            <router-link to="/contact">
              <img id="email" src="../assets/email.png" alt="" />
            </router-link>
            <a href="https://www.linkedin.com/in/qimingchen91129/">
              <img id="linkedin" src="../assets/linkedin.png" alt="" />
            </a>
            <a href="https://www.github.com/QimingChen">
              <img id="github" src="../assets/github.png" alt="" />
            </a>
          </div>
        </div>
      </li>
      <li>
        <div id="intro-right-column">
          <div id="intro-text">
            <p> I am an NYC-based Software Developer extremely motivated in bringing Data-Driven Applications to enhance clients’ quality of life with technology. </p>
            <p>Given my interdisciplinary background in Statistics and Computer Science, I worked cross-functionally together with data scientists and developers to bring valuable data insights to the product.</p>
            <p>With 2+ years of experience in software development, my detail-oriented and process-oriented working style enhanced the efficiency in development and troubleshooting. I am hoping to bring my skills and experience to the position.</p>
            <p>I am looking forward to hearing from you!!</p>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'HomeIntro'
}

</script>

<style scoped>

li{
  display: inline;
}


div#intro{
position:relative;
top:10px;
}

div#intro-left-column{
  width:25%;
  float:left;
}

div#intro-right-column{
  padding-left: 1%;
  padding-right:1%;
  border-style: solid;
  border-color:black;
  border-radius: 10px;
  width:70%;
  float:right;
  animation: shadow-moving 10s linear infinite;
}

div#intro-text{
  text-align: left;
}

@media screen and (max-width: 600px) {
  div#intro-left-column{
    width:100%;
    float:center;
  }

  div#intro-right-column{
    width:100%;
    float:center;

  }
}

img#round-image{
    background: white;
    background-repeat: no-repeat;
    background-size: cover;
    overflow:hidden;
    border-radius:30%;
    width:150px;
    height:150px;
}

img#round-image:hover{
  animation: round-square 5s linear infinite;
}

@keyframes round-square {
    0% { border-radius:30%; }
    25% { border-radius:50%; }
    50% { border-radius:10%;  }
    75% { border-radius:50%; }
    100% { border-radius:30%;  }
}

img#linkedin, img#github, img#email{
    width:40px;
    height:40px;
}

img#linkedin:hover, img#github:hover, img#email:hover{
  border-style: solid;
  border-color:rgb(220, 243, 9);
  border-radius: 10px;
}

button#contactme{
    width: 80px;
    height: 30px;
    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

button#contactme:before{
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

button#contactme:active {
    color: #000
}

button#contactme:active:after {
    background: transparent;
}

button#contactme:hover:before {
    opacity: 1;
}

button#contactme:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}

@keyframes shadow-moving {
  0% { box-shadow: 0px 0px 0px 0px black; }
  25% { box-shadow: 2px 2px 5px 2px gold; }
  50% { box-shadow: 2px 2px 2px 5px yellow; } 
  75% { box-shadow: 2px 5px 2px 2px gold; } 
  100% {box-shadow: 0px 0px 0px 0px black; } 
}
</style>


