<template>
  <div class="resume">
    <div id="name-title">
     <h1 id="name">Qiming Chen</h1>
     <h3 id="title">Software Developer|Data Scientist</h3>
    </div>
    <div class="download">
      <a id="download-btn" style="width:100%" type="submit" href="https://pdfhost.io/edit?doc=dafb26e2-72f1-443b-aa4a-ebe4488560af"><i class="fa fa-download"></i> Download Kyle's Resume</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Resume'
}
</script>

<style scoped>

div#name-title{
border-style: solid;
border-width: 5px;
border-color:#333;
border-radius: 10px;
margin-top: 20px;
}

div.resume{
padding-left: 10%;
padding-right: 10%;
}

div#name-title{
-webkit-animation: slide-down .5s ease-out;
-moz-animation: slide-down .5s ease-out;
}

/* Style buttons */
#download-btn {
  width: 100%;
  position: relative;
  top:50px;
  background-color: DodgerBlue;
  border: none;
  color: white;
  padding: 12px 30px;
  cursor: pointer;
  font-size: 20px;
  -webkit-animation: fade-in 4s ease-out;
  -moz-animation: fade-in 4s ease-out;
}

/* Darker background on mouse-over */
#download-btn:hover {
  background-color: RoyalBlue;
}

@-webkit-keyframes slide-down {
0% { opacity: 0; -webkit-transform: translateY(-100%); }
100% { opacity: 1; -webkit-transform: translateY(0); }
}
@-moz-keyframes slide-down {
0% { opacity: 0; -moz-transform: translateY(-100%); }
100% { opacity: 1; -moz-transform: translateY(0); }
}

@-webkit-keyframes fade-in {
  from { opacity: 0; }
  to   { opacity: 1; }
}
@-moz-keyframes fade-in {
  from { opacity: 0; }
  to   { opacity: 1; }
}


</style>



