<template>
<div class="container">
  <div style="text-align:center">
    <h2>Contact Me</h2>
    <p>Feel free to leave me a message, will circle back soon.</p>
  </div>
  <div class="row"> 
    <div class="column">
      <form action="mailto:qimingchen91129@gmail.com" method="post" enctype="text/plain">
        <p>What's your name please?: <input type="text" name="name"/></p>
        <p>What's your email/phone number I can circle back?: <input type="text" name="contactinfo"/></p>
        <p>Message:
        <br />
        <textarea name="message" rows = "12" cols = "35" placeholder="Send me a brief message."></textarea>
        <br>
        <p>
          <input type="submit" name="submit" value="Send To Kyle" />
        </p>
      </form>
    </div>
  </div>
</div>
</template>

<script>

export default {
  name: 'Contact'
}
</script>

<style scoped>
* {
  box-sizing: border-box;
}

/* Style inputs */
input[type=text], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 20px;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=submit] {
  width: 100%;
  background-color: #4CAF50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 18px;
}

input[type=submit]:hover {
  background-color: #45a049;
}

/* Style the container/contact section */
.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 10px;
}

/* Create two columns that float next to eachother */
.column {
  float: left;
  width: 100%;
  margin-top: 6px;
  padding: 20px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .column, input[type=submit] {
    width: 100%;
    margin-top: 0;
  }
}
</style>



